import React, { Component } from "react";
import Aboutme from "./aboutme";
import Reveal, { RevealRight, RevealOnly } from "./Reveal";
import Github from "../Pics/github.png";
import Linkedin from "../Pics/linkedin.png";

class Coverpage extends Component {
  render() {
    return (
      <div className="together">
        <div className="container">
          <div style={{ background: "tomato", color: "white" }}>
            <div className="first">
              <Reveal>
                <h1>It's Anthony Hsia.</h1>

                <RevealOnly>
                  <h3>A computer scientist.</h3>
                </RevealOnly>
              </Reveal>
            </div>
            <div className="second">
              <RevealOnly>(:</RevealOnly>
            </div>
            <div className="arrow-down">(continue scrolling..)</div>
          </div>
          <div>
            <RevealOnly>
              <Aboutme></Aboutme>
            </RevealOnly>
          </div>
          <div>
            <Reveal>FOOD</Reveal>
          </div>
          <div>
            <Reveal>ESPORTS</Reveal>
          </div>
        </div>
        <div className="rightside_container">
          <div className="rightside_items">
            <div></div>
            <div>
              <a href="https://github.com/ahsia3" target="_blank">
                <img src={Github} width="25px" height="25px" alt="github"></img>
              </a>
            </div>
            <br />
            <div>
              <a
                href="https://www.linkedin.com/in/anthonyhsia22/"
                target="_blank"
              >
                <img
                  src={Linkedin}
                  width="25px"
                  height="25px"
                  alt="Linkedin"
                ></img>
              </a>
            </div>
            <div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default Coverpage;
